.authwrapper {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url('/src/assets/images/bg.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

.container {
  background: rgb(130, 218, 132);
  position: relative;
  width: 50%;
  max-width: 400px;
  height: 65vh;
  border-radius: 20px;
  overflow: hidden;
  text-align: center;

}

.signIn,
.signUp {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: 0.3s ease-in-out;
  position: absolute;
  top: 0;
  left: 0;
}

.signIn {
  transform: translateY(0);
  z-index: 2;
}

.signUp {
  transform: translateY(100%);
  z-index: 1;
}

.signUp select {
  width: 50%;
  height: 1.5rem;
  border: none;
  outline: none;
  background: transparent;
  font-size: 0.9rem;
  color: #000;
  margin-bottom: 0.3rem;
}

.container.active .signIn {
  transform: translateY(-100%);
  z-index: 1;
}

.container.active .signUp {
  transform: translateY(0);
  z-index: 2;
}



.input_grp {
  font-size: 1rem;
  position: relative;
  margin: 25px 0;
  border-bottom: 2px solid #fff;
}

.input_grp input {
  width: 100%;
  height: 1.5rem;
  border: none;
  outline: none;
  background: transparent;
  font-size: 0.9rem;
}

.input_grp label {
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
  pointer-events: none;
  font-size: 1rem;
  color: #000;
  transition: all 0.3s ease;
}

.input_grp input:focus~label,
.input_grp input:valid~label {
  top: -5px;
  color: dimgray;
}

#login-eye,
#signUp-eye {
  right: 0.3rem;
  position: absolute;
  cursor: pointer;
  top: 22%;
}

.remember {
  display: flex;
  align-items: center;
  font-size: 0.8rem;
}

.remember label {
  margin-left: 3px;
  margin-right: auto;
  cursor: pointer;
  color: #000;
}

.btn {
  position: relative;
  padding: 0.5vw 1.2vw;
  font-size: 1.5rem;
  border-radius: 25px;
  cursor: pointer;
  border: none;
  color: black;
  background: white;

  margin-top: 15px;
}

.btn:hover {
  color: white;
  background: black;
}

.signIn_btn,
.signUp_btn {
  text-align: center;
  font-size: 0.9rem;
  margin: 15px 0;
}

.signIn_btn a,
.remember a,
.signUp_btn a {
  text-decoration: none;
}

.signIn_btn a:hover,
.remember a:hover,
.signUp_btn a:hover {
  cursor: pointer;
  color: blue;
  text-decoration: underline;
}